import React from 'react';
import { IStackItemStyles, PrimaryButton, Stack, StackItem } from '@fluentui/react';

type GlassProps = {
    contents: Array<string>,
}
type GlassState = {
}
export default class Glass extends React.Component<GlassProps, GlassState>
{
    constructor(props: any) {
        super(props);
    }

    styles: IStackItemStyles = {
        root: {
            padding: 5,
          },
      };
    render() {
        return <Stack >
            Glass Contents:
            {this.props.contents.map((item, idx) =>
                <StackItem styles={this.styles} key={idx}>
                    {item}
                </StackItem>)}
        </Stack>
    }
}