import { IStackItemStyles, PrimaryButton, Stack, StackItem } from '@fluentui/react';
import React from 'react';
import * as reference from './Reference'

type ActionsProps = {
    list: Array<number>,
    submitAction: Function
}
type ActionsState = {
}

export default class Actions extends React.Component<ActionsProps, ActionsState>
{
    constructor(props: any) {
        super(props);
      }

      styles: IStackItemStyles = {
        root: {
            padding: 5,
          },
      };

    render(){
    return <div>
        <Stack>
        {this.props.list.map((item, idx)=> <StackItem styles={this.styles} key={idx}>
            <PrimaryButton onClick={()=>{this.props.submitAction(item)}}>{reference.Volumes[item]}</PrimaryButton>
        </StackItem>)}
            </Stack></div>
    }
}