import { IStackItemStyles, PrimaryButton, Stack, StackItem } from '@fluentui/react';
import React from 'react';

type TargetsState = {
    count: number, 
    list: Array<number>,

}
type TargetsProps = {
    list: Array<number>, 
    updateParent: Function,
    disabledList: Array<number>,
    selected: number
}

export default class Targets extends React.Component<TargetsProps, TargetsState>
{
    constructor(props: any) {
        super(props);
      }

      styles: IStackItemStyles = {
        root: {
            padding: 5,
          },
      };
    render(){
        return <div>
            <Stack horizontal>
                {this.props.list.map((_, idx) =>
                !this.props.disabledList.includes(idx) ? 
                <StackItem styles={this.styles} key={idx}>
                    <PrimaryButton onClick={()=>{this.props.updateParent(idx); console.log(this.props.disabledList, idx)}} hidden={this.props.disabledList.includes(idx)} disabled={this.props.selected == idx }>{idx}</PrimaryButton>
                </StackItem>
                : 
                "")}
            </Stack>
            </div>
    }
}