import React from 'react';
import Targets from './Targets';
import Actions from './Actions';

type InputsState = {
    selectedTarget: number,
}

type InputsProps = {
    actionsList: Array<number>,
    targetsList: Array<number>,
    targetsDisabledList: Array<number>,
    actionFunction: Function
}

export default class Inputs extends React.Component<InputsProps, InputsState>
{
    constructor(props: any) {
        super(props);
        this.state = {
            selectedTarget: 0,
        }
        this.change = this.change.bind(this);
    }

    change(x:number){
        this.setState({selectedTarget: x})
    }

    render(){
        return <div>
            <label>Target Cups:</label>
            <Targets list={this.props.targetsList} disabledList={this.props.targetsDisabledList} updateParent={(x:number)=>this.change(x)} selected={this.state.selectedTarget}/>
            <label>Actions:</label>
            <Actions list={this.props.actionsList} submitAction={(action:number)=>this.props.actionFunction(action, (this.props.targetsDisabledList.includes(this.state.selectedTarget))? -1 : this.state.selectedTarget)}/>
        </div>
    }
}