import React, { FormEvent } from 'react';
import logo from './logo.svg';
import './App.css';
import { Stack, IStackStyles, IStackTokens, IStackItemStyles } from 'office-ui-fabric-react/lib/Stack';
import Recipe from './Recipe';
import * as Reference from './Reference';
import Glass from './Glass';
import { DefaultButton, PrimaryButton, TextField} from '@fluentui/react';
import Inputs from './Inputs';
import * as signalR from "@microsoft/signalr";
import * as querystring from "query-string";

type AppState = {
  hubConnection: signalR.HubConnection,
  glassContents: Array<number>,
  phase: GamePhase,
  playerCount: number,
  actions: Array<number>,
  roundNumber: number,
  cupNumber: number,
  results: Array<number>,
  teamName: string,
  cupLocks: Array<number>,
  resultsList: Array<Array<number>>,
  blockJoin: boolean,
  final: boolean
}

type AppProps = {
  actionsList: Array<string>,
  targetsList: Array<number>,
  actionFunction: Function
}

enum GamePhase{
  Unassigned,
  Assigned,
  Started,
  Resulted
}

export default class App extends React.Component<any, AppState>
{ 
  constructor(props: any) {
    super(props);
    var roomcode = querystring.parse(window.location.search);
    var room = roomcode["room"];
    const hubConnection = new signalR.HubConnectionBuilder()
    .withUrl(`https://schittalkin.azurewebsites.net/whiterushin/`)
    .configureLogging(signalR.LogLevel.Information)
    .build();

    this.state = {
      hubConnection: hubConnection,
      glassContents: [],
      playerCount: 0,
      actions: [],
      roundNumber: -1,
      cupNumber: -1,
      phase: GamePhase.Unassigned,
      results: [],
      teamName: "",
      cupLocks: [],
      resultsList: [],
      blockJoin: false,
      final:false
    }
    this.componentDidMount.bind(this);
    //this.setupHub.bind(this);
    this.renderGame.bind(this);
    console.log(hubConnection);
  }

  async componentDidMount(){
    await this.state.hubConnection.start();
    this.setupHub(this.state.hubConnection);
  }

  reset(){
    this.setState({
      glassContents: [],
      results: [],
      cupLocks: []
    });
  }

  setupHub(connection: signalR.HubConnection){
    connection.on("Assign", (cup: number, actions: number[], playercount: number, round: number) => {
      console.log(`cup: ${cup} actions: ${actions} playercount: ${playercount} round number: ${round}`);
      this.setState({
        playerCount: playercount,
        roundNumber: round,
        cupNumber: cup,
        actions: actions
      });
    });
    connection.on("Start", () => {
      console.log(`started`);
      this.setState({
        phase: GamePhase.Started
      });
      this.reset();
    });
    connection.on("UpdateCupState", (cupState:number[]) => {
      console.log(`CupState: ${cupState}`);
      this.setState({
        glassContents: cupState
      });
    });
    connection.on("Result", (result:number[]) => {
      for (let i = result.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [result[i], result[j]] = [result[j], result[i]];
      }
      console.log(result);
      var count = result.reduce((x,y)=>x+y);
      if(count == 0){
        this.setState({final:true})
      }
      var list = [0,0,0,0]
      /*result.forEach(element => {
        list[element] += 1;
      });
      let list2 = this.state.resultsList;
      list2.push(list);*/
      this.setState({
        phase: GamePhase.Resulted,
        results: result,
      })
    });
    connection.on("LockCup", (cup:number) => {
      console.log(`Locked ${cup}`);
      this.setState({
        cupLocks: this.state.cupLocks.concat([cup]),
      })
      console.log(this.state.cupLocks);
    });
  };

  renderGame(){
    switch(this.state.phase){
      case GamePhase.Unassigned:
        return <div>
          <TextField placeholder="Team Name" onChange={(event: FormEvent<HTMLInputElement | HTMLTextAreaElement>, newValue:string|undefined)=>{this.setState({teamName: newValue??""})}}/>
          <DefaultButton onClick={()=>{console.log(this.state.teamName);this.state.hubConnection.invoke("Register", this.state.teamName); this.setState({blockJoin: true})}} text="Join Game" disabled={this.state.blockJoin}/>
          {this.state.blockJoin? "Waiting for other bartenders":""}
        </div>
      case GamePhase.Assigned:
        return <div>
          Please wait for the other bartenders to arrive
        </div>
      case GamePhase.Started:
        return <div>
          <label>You are cup # {this.state.cupNumber}</label>
          <Inputs targetsList={ [...Array(this.state.playerCount).keys()]} targetsDisabledList={this.state.cupLocks} actionsList={this.state.actions} actionFunction={(action:number, target:number)=>this.state.hubConnection.invoke("SendAction", action, target)}/>
          <Glass contents={this.state.glassContents.map((item: number)=> Reference.Volumes[item])}/>
          <PrimaryButton text="Empty glass" disabled={this.state.cupLocks.includes(this.state.cupNumber)} onClick={()=>this.state.hubConnection.invoke("EmptyCup")} />
          <DefaultButton text="Serve Drink" disabled={this.state.cupLocks.includes(this.state.cupNumber)} onClick={()=>{
            this.state.hubConnection.invoke("SubmitCup");
          }}/>
        </div> 
      case GamePhase.Resulted:
        return <div>
        <h1>Customer comments</h1>
        {this.state.results.map((item)=> 
        {
          if(item == 0){
              return <div>"Just like I ordered! Thanks!"</div>
                      }
          else if(item == 1){return <div>"Do I look like someone who would order this?"</div>}
          else if(item == 2){return <div>"I didn't even know this was on the menu!"</div>}
          else if(item == 3){return <div>"*spits out drink* Do you even know how to make drinks?"</div>}
        }
        )}
        {this.state.final? "You did it! Ask GC for your receipt":<PrimaryButton text="Reset" onClick={()=>{console.log(this.state.hubConnection);this.state.hubConnection.invoke("Reset"); }} />}
        </div>
    }
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <title>White Rushin</title>
          {this.renderGame()}
        </header>
      </div>
    );
  }
}
